.animate {
    -webkit-animation: 0.490 goDown;
            animation: 0.490 goDown;
}

@-webkit-keyframes goDown {
    from {
        bottom: 0%
    }
    to {
        bottom: 100%
    }
}

@keyframes goDown {
    from {
        bottom: 0%
    }
    to {
        bottom: 100%
    }
}


